.page-container {
  max-width: 1000px;
  margin: 0 auto;
  padding: 20px;
}

.page-header {
  margin-bottom: 30px;
}

.page-header h1 {
  color: #0078d4;
  margin-bottom: 15px;
}

.page-content {
  background: white;
  border-radius: 8px;
  padding: 30px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
}

.section-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
}

.section-header h2 {
  font-size: 1.5em;
  color: #333;
  margin: 0;
}

.badge-required {
  background: #0078d4;
  color: white;
  padding: 6px 12px;
  border-radius: 4px;
  font-size: 0.8em;
  font-weight: bold;
}

.why-keyvault {
  background: #f0f7ff;
  border-left: 4px solid #0078d4;
  padding: 20px;
  margin-bottom: 30px;
  border-radius: 4px;
}

.why-keyvault h3 {
  margin-top: 0;
  color: #0078d4;
}

.benefits-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 15px;
  margin-top: 20px;
}

.benefit-card {
  background: white;
  border: 1px solid #ddd;
  border-radius: 6px;
  padding: 15px;
  text-align: center;
}

.benefit-icon {
  font-size: 2em;
  display: block;
  margin-bottom: 10px;
}

.benefit-card h4 {
  margin: 10px 0;
  color: #333;
}

.benefit-card p {
  font-size: 0.9em;
  color: #666;
  line-height: 1.4;
}

.cost-section {
  background: #e8f5e9;
  border: 2px solid #4caf50;
  border-radius: 6px;
  padding: 20px;
  margin-bottom: 30px;
  text-align: center;
}

.cost-section h3 {
  margin-top: 0;
  color: #2e7d32;
}

.cost-value {
  font-size: 2em;
  font-weight: bold;
  color: #2e7d32;
  margin: 10px 0;
}

.cost-note {
  font-size: 0.9em;
  color: #666;
  font-style: italic;
}

.naming-section {
  background: #fff3cd;
  border-left: 4px solid #ffc107;
  padding: 15px 20px;
  margin-bottom: 30px;
  border-radius: 4px;
}

.naming-section h3 {
  margin-top: 0;
  color: #f57c00;
}

.naming-section code {
  background: #333;
  color: #4ec9b0;
  padding: 3px 6px;
  border-radius: 3px;
  font-family: 'Consolas', 'Monaco', monospace;
}

.pattern-explanation {
  margin-top: 10px;
  font-size: 0.95em;
  line-height: 1.6;
}

.usage-section {
  margin-bottom: 30px;
}

.usage-section h3 {
  color: #333;
  border-bottom: 2px solid #0078d4;
  padding-bottom: 10px;
  margin-bottom: 20px;
}

.command-block {
  margin-bottom: 30px;
  padding: 20px;
  background: #f9f9f9;
  border-radius: 6px;
  border: 1px solid #ddd;
}

.command-block h4 {
  margin-top: 0;
  color: #0078d4;
}

.command-description {
  color: #666;
  margin-bottom: 15px;
  font-size: 0.95em;
}

.cli-command {
  background: #1e1e1e;
  color: #d4d4d4;
  padding: 12px;
  border-radius: 4px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: 'Consolas', 'Monaco', monospace;
  font-size: 0.85em;
  margin: 10px 0;
}

.cli-command code {
  flex: 1;
  word-break: break-all;
}

.cli-command button {
  background: #0078d4;
  color: white;
  border: none;
  padding: 6px 12px;
  border-radius: 4px;
  cursor: pointer;
  margin-left: 10px;
  white-space: nowrap;
  font-size: 0.9em;
}

.cli-command button:hover {
  background: #005a9e;
}

.cli-command-multiline {
  background: #1e1e1e;
  border-radius: 4px;
  padding: 15px;
  margin: 15px 0;
  position: relative;
}

.cli-title {
  color: #4ec9b0;
  font-weight: bold;
  margin-bottom: 10px;
  font-family: 'Consolas', 'Monaco', monospace;
}

.cli-command-multiline pre {
  margin: 0;
  overflow-x: auto;
}

.cli-command-multiline code {
  color: #d4d4d4;
  font-family: 'Consolas', 'Monaco', monospace;
  font-size: 0.85em;
  line-height: 1.5;
}

.cli-command-multiline button {
  position: absolute;
  top: 15px;
  right: 15px;
  background: #0078d4;
  color: white;
  border: none;
  padding: 6px 12px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 0.85em;
}

.cli-command-multiline button:hover {
  background: #005a9e;
}

.command-note {
  font-size: 0.85em;
  color: #666;
  font-style: italic;
  margin-top: 8px;
}

.command-note code {
  background: #333;
  color: #4ec9b0;
  padding: 2px 5px;
  border-radius: 3px;
}

.best-practices {
  background: #e3f2fd;
  border-left: 4px solid #2196f3;
  padding: 20px;
  border-radius: 4px;
}

.best-practices h3 {
  margin-top: 0;
  color: #1976d2;
}

.best-practices ul {
  margin: 10px 0;
  padding-left: 20px;
}

.best-practices li {
  margin: 8px 0;
  line-height: 1.6;
}

.page-actions {
  display: flex;
  justify-content: space-between;
  margin-top: 30px;
  padding: 20px 0;
}

.btn-primary {
  background: #0078d4;
  color: white;
  border: none;
  padding: 12px 24px;
  font-size: 1em;
  border-radius: 4px;
  cursor: pointer;
  transition: background 0.3s;
}

.btn-primary:hover {
  background: #005a9e;
}

.btn-secondary {
  background: #f3f3f3;
  color: #333;
  border: 1px solid #ccc;
  padding: 12px 24px;
  font-size: 1em;
  border-radius: 4px;
  cursor: pointer;
  transition: background 0.3s;
}

.btn-secondary:hover {
  background: #e0e0e0;
}

.page-footer {
  text-align: center;
  margin-top: 30px;
  padding: 20px;
  color: #666;
  font-size: 0.9em;
  border-top: 1px solid #eee;
}
