.progress-bar-container {
  margin-bottom: 2rem;
}

.progress-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.progress-header h2 {
  margin: 0;
  color: #0078d4;
  font-size: 1.5rem;
}

.progress-percentage {
  font-size: 1.25rem;
  font-weight: 600;
  color: #0078d4;
}

.progress-bar {
  width: 100%;
  height: 12px;
  background-color: #e1dfdd;
  border-radius: 6px;
  overflow: hidden;
  margin-bottom: 1.5rem;
}

.progress-fill {
  height: 100%;
  background: linear-gradient(90deg, #0078d4, #005a9e);
  transition: width 0.3s ease;
}

.progress-steps {
  display: flex;
  justify-content: space-between;
  gap: 1rem;
}

.progress-step {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  opacity: 0.5;
  transition: opacity 0.3s;
}

.progress-step.active {
  opacity: 1;
}

.step-indicator {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  background-color: #e1dfdd;
  color: #605e5c;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 600;
  margin-bottom: 0.5rem;
  transition: all 0.3s;
}

.progress-step.active .step-indicator {
  background-color: #0078d4;
  color: white;
}

.progress-step.current .step-indicator {
  background-color: #0078d4;
  color: white;
  box-shadow: 0 0 0 4px rgba(0, 120, 212, 0.2);
  transform: scale(1.1);
}

.step-label {
  font-size: 0.875rem;
  color: #605e5c;
  text-align: center;
}

.progress-step.active .step-label {
  color: #323130;
  font-weight: 600;
}
