.page-container { max-width: 1000px; margin: 0 auto; padding: 20px; }
.page-header { margin-bottom: 30px; }
.page-header h1 { color: #0078d4; margin-bottom: 15px; }
.page-content { background: white; border-radius: 8px; padding: 30px; box-shadow: 0 2px 8px rgba(0,0,0,0.1); }
.section-header { display: flex; justify-content: space-between; align-items: center; margin-bottom: 30px; }
.section-header h2 { font-size: 1.5em; color: #333; margin: 0; }
.badge-optional { background: #666; color: white; padding: 6px 12px; border-radius: 4px; font-size: 0.8em; font-weight: bold; }
.what-it-does { background: #f0f7ff; border-left: 4px solid #0078d4; padding: 15px 20px; margin-bottom: 20px; border-radius: 4px; }
.what-it-does h3 { margin-top: 0; color: #0078d4; }
.what-it-does ul { margin: 10px 0; padding-left: 20px; }
.what-it-does li { margin: 6px 0; line-height: 1.5; }
.cost-section { background: #fff3cd; border: 2px solid #ffc107; border-radius: 6px; padding: 20px; margin-bottom: 30px; text-align: center; }
.cost-section h3 { margin-top: 0; color: #f57c00; }
.cost-value { font-size: 2em; font-weight: bold; color: #f57c00; margin: 10px 0; }
.cost-note { font-size: 0.9em; color: #666; font-style: italic; }
.recommendation { padding: 20px; border-radius: 6px; margin-bottom: 30px; }
.recommendation h3 { margin-top: 0; }
.recommendation p { line-height: 1.6; }
.recommendation.production { background: #e8f5e9; border-left: 4px solid #4caf50; }
.recommendation.production h3 { color: #2e7d32; }
.recommendation.non-prod { background: #e3f2fd; border-left: 4px solid #2196f3; }
.recommendation.non-prod h3 { color: #1976d2; }
.when-to-use, .when-to-skip { background: #f5f5f5; border: 1px solid #ddd; padding: 20px; border-radius: 6px; margin-bottom: 20px; }
.when-to-use h3, .when-to-skip h3 { margin-top: 0; color: #333; }
.when-to-use ul, .when-to-skip ul { margin: 10px 0; padding-left: 20px; }
.when-to-use li, .when-to-skip li { margin: 6px 0; line-height: 1.5; }
.decision-section { background: #fafafa; border: 2px solid #0078d4; padding: 25px; border-radius: 8px; }
.decision-section h3 { margin-top: 0; color: #0078d4; }
.checkbox-container { margin: 20px 0; }
.checkbox-label { display: flex; align-items: center; cursor: pointer; font-size: 1.1em; }
.checkbox-label input[type="checkbox"] { width: 24px; height: 24px; margin-right: 12px; cursor: pointer; }
.selection-info { margin-top: 20px; padding: 15px; background: white; border-radius: 4px; border-left: 4px solid #0078d4; }
.selection-info code { background: #333; color: #4ec9b0; padding: 3px 6px; border-radius: 3px; font-family: 'Consolas', 'Monaco', monospace; }
.naming-example { margin-top: 10px; font-size: 0.95em; color: #666; }
.page-actions { display: flex; justify-content: space-between; margin-top: 30px; padding: 20px 0; }
.btn-primary { background: #0078d4; color: white; border: none; padding: 12px 24px; font-size: 1em; border-radius: 4px; cursor: pointer; transition: background 0.3s; }
.btn-primary:hover { background: #005a9e; }
.btn-secondary { background: #f3f3f3; color: #333; border: 1px solid #ccc; padding: 12px 24px; font-size: 1em; border-radius: 4px; cursor: pointer; transition: background 0.3s; }
.btn-secondary:hover { background: #e0e0e0; }
.page-footer { text-align: center; margin-top: 30px; padding: 20px; color: #666; font-size: 0.9em; border-top: 1px solid #eee; }
