.page-container {
  max-width: 1000px;
  margin: 0 auto;
  padding: 20px;
}

.page-header {
  text-align: center;
  margin-bottom: 40px;
}

.page-header h1 {
  font-size: 2.5em;
  margin-bottom: 10px;
  color: #0078d4;
}

.subtitle {
  font-size: 1.1em;
  color: #666;
  max-width: 700px;
  margin: 0 auto;
}

.page-content {
  background: white;
  border-radius: 8px;
  padding: 30px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
}

.info-section {
  margin-bottom: 40px;
}

.info-section h2 {
  font-size: 1.8em;
  margin-bottom: 20px;
  color: #333;
  border-bottom: 2px solid #0078d4;
  padding-bottom: 10px;
}

.checklist {
  list-style: none;
  padding: 0;
  font-size: 1.1em;
}

.checklist li {
  padding: 10px 0;
  border-bottom: 1px solid #eee;
}

.resources-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 20px;
  margin-top: 20px;
}

.resource-item {
  border: 2px solid #ddd;
  border-radius: 8px;
  padding: 20px;
  background: #fafafa;
  position: relative;
}

.resource-item.required {
  border-color: #0078d4;
  background: #f0f7ff;
}

.resource-item.optional {
  border-color: #ccc;
}

.badge {
  position: absolute;
  top: 10px;
  right: 10px;
  background: #0078d4;
  color: white;
  padding: 4px 8px;
  border-radius: 4px;
  font-size: 0.7em;
  font-weight: bold;
}

.resource-item.optional .badge {
  background: #666;
}

.resource-item h3 {
  margin-top: 10px;
  margin-bottom: 10px;
  color: #333;
  font-size: 1.2em;
}

.resource-item p {
  margin: 5px 0;
  font-size: 0.9em;
  color: #666;
}

.cost {
  font-weight: bold;
  color: #0078d4 !important;
  margin-top: 10px !important;
}

.steps-list {
  font-size: 1.1em;
  line-height: 1.8;
}

.steps-list li {
  margin-bottom: 10px;
}

.cta-section {
  text-align: center;
  margin-top: 40px;
}

.btn-primary {
  background: #0078d4;
  color: white;
  border: none;
  padding: 12px 30px;
  font-size: 1.1em;
  border-radius: 4px;
  cursor: pointer;
  transition: background 0.3s;
}

.btn-primary:hover {
  background: #005a9e;
}

.btn-large {
  padding: 15px 40px;
  font-size: 1.3em;
}

.page-footer {
  text-align: center;
  margin-top: 40px;
  padding: 20px;
  color: #666;
  font-size: 0.9em;
  border-top: 1px solid #eee;
}
