.page-container {
  max-width: 900px;
  margin: 0 auto;
  padding: 20px;
}

.page-header {
  margin-bottom: 30px;
}

.page-header h1 {
  color: #0078d4;
  margin-bottom: 15px;
}

.page-content {
  background: white;
  border-radius: 8px;
  padding: 30px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
}

.page-content h2 {
  font-size: 1.5em;
  color: #333;
  margin-bottom: 25px;
}

.form-section {
  margin-bottom: 30px;
}

.form-group {
  margin-bottom: 20px;
}

.form-group label {
  display: block;
  font-weight: bold;
  margin-bottom: 8px;
  color: #333;
}

.form-group select,
.form-group input[type="text"] {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 1em;
  box-sizing: border-box;
}

.form-group select:focus,
.form-group input[type="text"]:focus {
  outline: none;
  border-color: #0078d4;
}

.form-group small {
  display: block;
  margin-top: 5px;
  font-size: 0.85em;
  color: #666;
  font-style: italic;
}

.vm-name-section {
  background: #f9f9f9;
  border: 2px solid #0078d4;
  border-radius: 8px;
  padding: 20px;
  margin-top: 30px;
}

.vm-name-section h3 {
  margin-top: 0;
  color: #0078d4;
}

.derived-name {
  margin-bottom: 20px;
}

.derived-name label {
  display: block;
  font-weight: bold;
  margin-bottom: 8px;
  color: #333;
}

.name-display {
  background: #1e1e1e;
  color: #4ec9b0;
  padding: 12px;
  border-radius: 4px;
  font-family: 'Consolas', 'Monaco', monospace;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.name-display code {
  font-size: 1.1em;
}

.char-count {
  background: #333;
  color: #fff;
  padding: 4px 8px;
  border-radius: 4px;
  font-size: 0.85em;
}

.override-toggle {
  margin: 20px 0;
}

.override-toggle label {
  display: flex;
  align-items: center;
  cursor: pointer;
  font-weight: bold;
}

.override-toggle input[type="checkbox"] {
  margin-right: 8px;
  width: 18px;
  height: 18px;
  cursor: pointer;
}

.final-name-display {
  margin-top: 20px;
  padding: 15px;
  border-radius: 4px;
  border: 2px solid #ccc;
}

.final-name-display.valid {
  background: #e8f5e9;
  border-color: #4caf50;
}

.final-name-display.invalid {
  background: #ffebee;
  border-color: #f44336;
}

.final-name-label {
  font-size: 0.9em;
  color: #666;
  margin-bottom: 5px;
}

.final-name-value {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 1.3em;
}

.validation-icon {
  font-size: 1.5em;
}

.error-message {
  margin-top: 10px;
  color: #d32f2f;
  font-weight: bold;
  font-size: 0.9em;
}

.page-actions {
  display: flex;
  justify-content: space-between;
  margin-top: 30px;
  padding: 20px 0;
}

.btn-primary {
  background: #0078d4;
  color: white;
  border: none;
  padding: 12px 24px;
  font-size: 1em;
  border-radius: 4px;
  cursor: pointer;
  transition: background 0.3s;
}

.btn-primary:hover:not(:disabled) {
  background: #005a9e;
}

.btn-primary:disabled {
  background: #ccc;
  cursor: not-allowed;
}

.btn-secondary {
  background: #f3f3f3;
  color: #333;
  border: 1px solid #ccc;
  padding: 12px 24px;
  font-size: 1em;
  border-radius: 4px;
  cursor: pointer;
  transition: background 0.3s;
}

.btn-secondary:hover {
  background: #e0e0e0;
}

.page-footer {
  text-align: center;
  margin-top: 30px;
  padding: 20px;
  color: #666;
  font-size: 0.9em;
  border-top: 1px solid #eee;
}
