.character-counter {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.75rem 1rem;
  background-color: #f5f5f5;
  border-radius: 4px;
  border: 2px solid #e1dfdd;
  font-family: 'Courier New', monospace;
  font-size: 1.1rem;
  margin-top: 0.5rem;
  transition: all 0.3s;
}

.character-counter.valid {
  border-color: #107c10;
  background-color: #f0fdf0;
}

.character-counter.warning {
  border-color: #ffb900;
  background-color: #fff4ce;
}

.character-counter.invalid {
  border-color: #d13438;
  background-color: #fef0f0;
}

.counter-value {
  font-weight: 600;
  color: #323130;
  flex: 1;
}

.counter-stats {
  font-size: 0.9rem;
  font-weight: 600;
  white-space: nowrap;
  margin-left: 1rem;
}

.character-counter.valid .counter-stats {
  color: #107c10;
}

.character-counter.warning .counter-stats {
  color: #d83b01;
}

.character-counter.invalid .counter-stats {
  color: #d13438;
}
