.page-container {
  max-width: 900px;
  margin: 0 auto;
  padding: 20px;
}

.page-header {
  margin-bottom: 30px;
}

.page-header h1 {
  color: #0078d4;
  margin-bottom: 15px;
}

.page-content {
  background: white;
  border-radius: 8px;
  padding: 30px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
}

.section-header h2 {
  font-size: 1.5em;
  color: #333;
  margin-bottom: 20px;
}

.why-section {
  background: #f0f7ff;
  border-left: 4px solid #0078d4;
  padding: 15px 20px;
  margin-bottom: 30px;
  border-radius: 4px;
}

.why-section h3 {
  margin-top: 0;
  color: #0078d4;
}

.why-section p {
  margin: 10px 0;
  line-height: 1.6;
}

.run-from {
  font-size: 0.95em;
  font-style: italic;
  color: #666;
}

.sample-data-section {
  text-align: center;
  margin: 20px 0 30px 0;
}

.btn-sample {
  background: #107c10;
  color: white;
  border: none;
  padding: 12px 24px;
  font-size: 1em;
  border-radius: 4px;
  cursor: pointer;
  transition: background 0.3s;
}

.btn-sample:hover {
  background: #0d5f0d;
}

.cli-section {
  margin-bottom: 30px;
  border-bottom: 1px solid #eee;
  padding-bottom: 20px;
}

.cli-section:last-of-type {
  border-bottom: none;
}

.cli-section h3 {
  color: #333;
  margin-bottom: 10px;
}

.cli-command {
  background: #1e1e1e;
  color: #d4d4d4;
  padding: 12px;
  border-radius: 4px;
  margin: 10px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: 'Consolas', 'Monaco', monospace;
  font-size: 0.9em;
}

.cli-command code {
  flex: 1;
  word-break: break-all;
}

.cli-command button {
  background: #0078d4;
  color: white;
  border: none;
  padding: 6px 12px;
  border-radius: 4px;
  cursor: pointer;
  margin-left: 10px;
  white-space: nowrap;
}

.cli-command button:hover {
  background: #005a9e;
}

.form-group {
  margin: 15px 0;
}

.form-group label {
  display: block;
  font-weight: bold;
  margin-bottom: 8px;
  color: #333;
}

.form-group input {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 1em;
  box-sizing: border-box;
}

.form-group input:focus {
  outline: none;
  border-color: #0078d4;
}

.note {
  font-size: 0.9em;
  color: #666;
  font-style: italic;
  margin: 10px 0;
}

.page-actions {
  display: flex;
  justify-content: space-between;
  margin-top: 30px;
  padding: 20px 0;
}

.btn-primary {
  background: #0078d4;
  color: white;
  border: none;
  padding: 12px 24px;
  font-size: 1em;
  border-radius: 4px;
  cursor: pointer;
  transition: background 0.3s;
}

.btn-primary:hover {
  background: #005a9e;
}

.btn-secondary {
  background: #f3f3f3;
  color: #333;
  border: 1px solid #ccc;
  padding: 12px 24px;
  font-size: 1em;
  border-radius: 4px;
  cursor: pointer;
  transition: background 0.3s;
}

.btn-secondary:hover {
  background: #e0e0e0;
}

.page-footer {
  text-align: center;
  margin-top: 30px;
  padding: 20px;
  color: #666;
  font-size: 0.9em;
  border-top: 1px solid #eee;
}
