.page-container { max-width: 1000px; margin: 0 auto; padding: 20px; }
.page-header { margin-bottom: 30px; }
.page-header h1 { color: #0078d4; margin-bottom: 15px; }
.page-content { background: white; border-radius: 8px; padding: 30px; box-shadow: 0 2px 8px rgba(0,0,0,0.1); }
.page-content h2 { font-size: 1.5em; color: #333; margin-bottom: 25px; }
.info-section { background: #f0f7ff; border-left: 4px solid #0078d4; padding: 15px 20px; margin-bottom: 30px; border-radius: 4px; }
.info-section h3 { margin-top: 0; color: #0078d4; }
.info-section p { line-height: 1.6; margin: 0; }
.default-tags-section, .custom-tags-section { margin-bottom: 40px; }
.default-tags-section h3, .custom-tags-section h3 { color: #333; border-bottom: 2px solid #0078d4; padding-bottom: 10px; margin-bottom: 15px; }
.section-note { font-size: 0.95em; color: #666; margin-bottom: 15px; }
.tags-grid { display: grid; grid-template-columns: repeat(auto-fill, minmax(280px, 1fr)); gap: 15px; }
.tag-item { background: #f5f5f5; border: 2px solid #ddd; border-radius: 6px; padding: 15px; display: flex; flex-direction: column; }
.tag-item.default { background: #e3f2fd; border-color: #2196f3; }
.tag-key { font-weight: bold; color: #0078d4; margin-bottom: 5px; font-size: 0.95em; }
.tag-value { color: #333; font-family: 'Consolas', 'Monaco', monospace; }
.examples-box { background: #fff3cd; border: 1px solid #ffc107; border-radius: 6px; padding: 15px; margin-bottom: 20px; }
.examples-box strong { color: #f57c00; }
.examples-box ul { margin: 10px 0; padding-left: 20px; }
.examples-box li { margin: 6px 0; }
.examples-box code { background: #333; color: #4ec9b0; padding: 2px 5px; border-radius: 3px; font-family: 'Consolas', 'Monaco', monospace; }
.custom-tags-list { margin-bottom: 20px; }
.custom-tag-row { display: flex; align-items: center; gap: 10px; margin-bottom: 15px; padding: 15px; background: #fafafa; border: 1px solid #ddd; border-radius: 6px; }
.tag-input { flex: 1; padding: 10px; border: 1px solid #ccc; border-radius: 4px; font-size: 1em; }
.tag-input.key { flex: 0 0 200px; }
.tag-input:focus { outline: none; border-color: #0078d4; }
.separator { font-weight: bold; color: #666; font-size: 1.2em; }
.btn-remove { background: #d32f2f; color: white; border: none; padding: 8px 12px; border-radius: 4px; cursor: pointer; font-size: 1em; transition: background 0.3s; }
.btn-remove:hover { background: #b71c1c; }
.btn-add-tag { background: #107c10; color: white; border: none; padding: 12px 24px; font-size: 1em; border-radius: 4px; cursor: pointer; transition: background 0.3s; }
.btn-add-tag:hover { background: #0d5f0d; }
.summary-section { background: #e8f5e9; border: 2px solid #4caf50; border-radius: 8px; padding: 20px; margin-top: 30px; }
.summary-section h3 { margin-top: 0; color: #2e7d32; }
.tags-preview { display: flex; flex-wrap: wrap; gap: 10px; }
.tag-preview-item { background: white; border: 1px solid #4caf50; border-radius: 4px; padding: 8px 12px; font-size: 0.95em; }
.tag-preview-item strong { color: #2e7d32; margin-right: 5px; }
.page-actions { display: flex; justify-content: space-between; margin-top: 30px; padding: 20px 0; }
.btn-primary { background: #0078d4; color: white; border: none; padding: 12px 24px; font-size: 1em; border-radius: 4px; cursor: pointer; transition: background 0.3s; }
.btn-primary:hover { background: #005a9e; }
.btn-secondary { background: #f3f3f3; color: #333; border: 1px solid #ccc; padding: 12px 24px; font-size: 1em; border-radius: 4px; cursor: pointer; transition: background 0.3s; }
.btn-secondary:hover { background: #e0e0e0; }
.page-footer { text-align: center; margin-top: 30px; padding: 20px; color: #666; font-size: 0.9em; border-top: 1px solid #eee; }
